import React from 'react';
import styled from 'styled-components';
import { Element } from 'react-scroll';

const BlockWrapper = styled.div`
    width: 100%;
    height: 200px;
`;

export default function ContentBlock(props) {
    const { children } = props;

    return (
        <BlockWrapper>
            {children}
        </BlockWrapper>
    );
};